import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
			list: [],
			pageNo: 1,
			isReach: false,
			loadText: '加载中...'
		};
	},
	onShow() {
		this.show();
	},
	computed: {
		...mapState(['app'])
	},

	onPullDownRefresh: function() {
		this.isReach = false;
		this.pageNo = 1;
		this.show();
	},
	onReachBottom: function() {
		this.pageNo++;
		this.isReach = true;
		this.show();
	},
	methods: {
		...mapMutations(['urlRequest']),
		TO() {
			uni.navigateTo({
				url: './inStorkDet/inStorkDet'
			});
		},
		show() {
			this.loadText = '加载中...';
			this.urlRequest({
				url: `/operation-agency-service/warehouseIn/getHeadList?access_token=${this.app.access_token}`,
				method: 'post',
				data: {
					isPage: true,
					pageNo: this.pageNo,
					pageSize: 10,
					params: { brandId: '', partsCode: '', warehouseId: '' }
				},
				callBack: res => {
					let a = res.data.result;
					if (a === '0000'||!a.length) {
						this.loadText = '没有更多数据！';
						return;
					} else if (this.isReach) {
						for(let i of a){
						this.list.push(i);
						}
						return;
					}
					this.list = a;
				}
			});
		}
	}
};
